 !important;\n}\n.mb-12{\n    margin-bottom: 12px !important;\n}\n\n.p-13{\n    padding: 13px !important;\n}\n.pt-24{\n    padding-top: 24px;\n}\n.p-0{\n    padding: 0px !important;\n}","@import \"scss/margin.scss\";\n@import \"scss/global.scss\";\n@import \"scss/colours.scss\";\n@import \"scss/fonts.scss\";\n@import \"scss/custom-light-theme.scss\";\n@import '~@angular/material/theming';\n@include mat-core();\n\n@import \"app/app.component\";\n@import \"app/nav-bar/nav-bar.component\";\n@import \"app/card/card.component\";\n@import \"scss/custom-dark-theme.scss\";\n@import \"app/card-audio/card-audio.component\";\n@import \"app/audio-card-modal/audio-card-modal.component\";\n@import \"app/audio-float-info/audio-float-info.component\";\n@import \"app/audio-card/audio-card.component\";\n@mixin custom-components-theme($theme,$text-theme) {\n  @include app-theme($theme, $text-theme);\n  @include nav-theme($theme, $text-theme);\n  @include card-theme($theme, $text-theme);\n  @include card-audio($theme, $text-theme);\n  @include card-audio-modal-theme($theme, $text-theme);\n  @include audio-card-theme($theme, $text-theme);\n  @include audio-float-theme($theme, $text-theme);\n}\n\n.light-theme {\n  @include angular-material-theme($custom-light-theme);\n  @include custom-components-theme($custom-light-theme,$custom-text-light-theme);\n}\n\n.dark-theme {\n  @include angular-material-theme($custom-dark-theme);\n  @include custom-components-theme($custom-dark-theme,$custom-text-dark-theme);\n}\n\nbody {\n  margin: 0;\n  padding: 0;\n}\n.dark-theme .mat-dialog-container {\n  box-shadow: 0px 11px 15px -7px rgba(0, 0, 0, 0.2), 0px 24px 38px 3px rgba(0, 0, 0, 0.14), 0px 9px 46px 8px rgba(0, 0, 0, 0.12);\n  background: #26272b;\n  color: rgba(0, 0, 0, 0.87);\n  border-radius: 25px;\n}\n.light-theme .mat-dialog-container {\n  box-shadow: 0px 11px 15px -7px rgba(0, 0, 0, 0.2), 0px 24px 38px 3px rgba(0, 0, 0, 0.14), 0px 9px 46px 8px rgba(0, 0, 0, 0.12);\n  background: #e0d9d2;\n  color: rgba(0, 0, 0, 0.87);\n  border-radius: 25px;\n}\n.mat-chip {\n  font-size: 11px;\n  font-weight: 500;\n}\n.light-theme .mat-chip.mat-standard-chip {\n  background-color: #706d69;\n  color: #e0d9d2;\n}\n.dark-theme .mat-chip.mat-standard-chip {\n  background-color: #b8b8b9;\n  color: #0b0b0b;\n}\n.light-theme .mat-progress-bar-background {\n  fill: #f47037 !important;\n}\n.dark-theme .mat-progress-bar-background {\n  fill: #f47037 !important;\n}\n.cdk-overlay-dark-backdrop {\n  background: #000000ad;\n}\n.light-theme .mat-slide-toggle-bar {\n  background-color: rgb(112 109 104);\n}",".pointer {\n    cursor: pointer;\n}\n\nhtml,body {\n    font-family: 'Montserrat', sans-serif;\n    color: #0B0B0B;\n}\n\n.displayNone{\n    display: none;\n}\n.displayInline{\n    display: block;\n}\n.visible{\n    visibility: visible;\n}\n.invisible{\n    visibility: hidden;\n}\n.grab:active {\n    cursor: -webkit-grabbing;\n    cursor: -moz-grabbing;\n}\n.grab {cursor: -webkit-grab; cursor: grab;}\n.grabbing {cursor: -webkit-grabbing; cursor: grabbing;}\n.col-resize {cursor: col-resize;}","$primary-light: (\n  #D6CCC1\n  contrast: (\n    50: #D6CCC1\n  )\n);\n\n.color-orange{\n  color: #f47037;\n}",".font-14{\n  font-size: 14px;\n}\n.font-9{\n  font-size: 9px;\n}\n.font-36{\n  font-size: 36px;\n}\n.font-24{\n  font-size: 24px;\n}\n.font-32{\n  font-size: 32px;\n}\n.fw-600{\n  font-weight: 600;\n}\n.font-merri{\n  font-family: 'Merriweather', serif;\n}\n.font-mont{\n  font-family: 'Montserrat', sans-serif;\n}\n.opacity-90{\n  opacity: 0.9;\n}\n.font-12{\n  font-size: 12px;\n}\n.underline{\n  text-decoration: underline;\n}","// File for which all imports are resolved and bundled. This is the entry-point for\n// the `@angular/material` theming Sass bundle. See `//src/material:theming_bundle`.\n\n// Import all the theming functionality.\n// We want overlays to always appear over user content, so set a baseline\n// very high z-index for the overlay container, which is where we create the new\n// stacking context for all overlays.\n$cdk-z-index-overlay-container: 1000 !default;\n$cdk-z-index-overlay: 1000 !default;\n$cdk-z-index-overlay-backdrop: 1000 !default;\n\n// Background color for all of the backdrops\n$cdk-overlay-dark-backdrop-background: rgba(0, 0, 0, 0.32) !default;\n\n// Default backdrop animation is based on the Material Design swift-ease-out.\n$backdrop-animation-duration: 400ms !default;\n$backdrop-animation-timing-function: cubic-bezier(0.25, 0.8, 0.25, 1) !default;\n\n\n@mixin cdk-overlay() {\n  .cdk-overlay-container, .cdk-global-overlay-wrapper {\n    // Disable events from being captured on the overlay container.\n    pointer-events: none;\n\n    // The container should be the size of the viewport.\n    top: 0;\n    left: 0;\n    height: 100%;\n    width: 100%;\n  }\n\n  // The overlay-container is an invisible element which contains all individual overlays.\n  .cdk-overlay-container {\n    position: fixed;\n    z-index: $cdk-z-index-overlay-container;\n\n    &:empty {\n      // Hide the element when it doesn't have any child nodes. This doesn't\n      // include overlays that have been detached, rather than disposed.\n      display: none;\n    }\n  }\n\n  // We use an extra wrapper element in order to use make the overlay itself a flex item.\n  // This makes centering the overlay easy without running into the subpixel rendering\n  // problems tied to using `transform` and without interfering with the other position\n  // strategies.\n  .cdk-global-overlay-wrapper {\n    display: flex;\n    position: absolute;\n    z-index: $cdk-z-index-overlay;\n  }\n\n  // A single overlay pane.\n  .cdk-overlay-pane {\n    // Note: it's important for this one to start off `absolute`,\n    // in order for us to be able to measure it correctly.\n    position: absolute;\n    pointer-events: auto;\n    box-sizing: border-box;\n    z-index: $cdk-z-index-overlay;\n\n    // For connected-position overlays, we set `display: flex` in\n    // order to force `max-width` and `max-height` to take effect.\n    display: flex;\n    max-width: 100%;\n    max-height: 100%;\n  }\n\n  .cdk-overlay-backdrop {\n    // TODO(jelbourn): reuse sidenav fullscreen mixin.\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n\n    z-index: $cdk-z-index-overlay-backdrop;\n    pointer-events: auto;\n    -webkit-tap-highlight-color: transparent;\n    transition: opacity $backdrop-animation-duration $backdrop-animation-timing-function;\n    opacity: 0;\n\n    &.cdk-overlay-backdrop-showing {\n      opacity: 1;\n\n      // In high contrast mode the rgba background will become solid so we need to fall back\n      // to making it opaque using `opacity`. Note that we can't use the `cdk-high-contrast`\n      // mixin, because we can't normalize the import path to the _a11y.scss both for the\n      // source and when this file is distributed. See #10908.\n      @media screen and (-ms-high-contrast: active) {\n        opacity: 0.6;\n      }\n    }\n  }\n\n  .cdk-overlay-dark-backdrop {\n    background: $cdk-overlay-dark-backdrop-background;\n  }\n\n  .cdk-overlay-transparent-backdrop {\n    // Note: as of Firefox 57, having the backdrop be `background: none` will prevent it from\n    // capturing the user's mouse scroll events. Since we also can't use something like\n    // `rgba(0, 0, 0, 0)`, we work around the inconsistency by not setting the background at\n    // all and using `opacity` to make the element transparent.\n    &, &.cdk-overlay-backdrop-showing {\n      opacity: 0;\n    }\n  }\n\n  // Overlay parent element used with the connected position strategy. Used to constrain the\n  // overlay element's size to fit within the viewport.\n  .cdk-overlay-connected-position-bounding-box {\n    position: absolute;\n    z-index: $cdk-z-index-overlay;\n\n    // We use `display: flex` on this element exclusively for centering connected overlays.\n    // When *not* centering, a top/left/bottom/right will be set whi